    grid-template-rows: 1fr 3fr;
    grid-template-columns: auto;
    min-height: 100vh;
    /* flex-direction: row; */
    /* color: white;
    align-items: center; */
  }

  .aboutMeHeading {
    /* position: absolute; */
    /* top: -100px; */
    /* top: -1000px; */
    min-width: 100vw;
    min-height: 100%;
  }

  .aboutMeContent {
    justify-self: center;
    width: 90vw;
    min-height: 100%;
    max-height: 100%;
  }
} ;
